section#projects {
    padding: 3rem 0;
    background-color: var(--highlight);

    #project-cards-container {
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;

        h2 {
            color: var(--background1);
            width: 100%;
            padding-bottom: 2rem;
        }
    }
}

section.project-card {
    border-radius: 8px;
    border: solid 3px var(--background2);
    //box-shadow: 0 0 10px 1px var(--background2);
    margin: 1rem auto;
    width: 30%;
    background-color: var(--background1);

    &:hover {
        box-shadow: 0 0 10px 3px var(--background2);
    }

    img {
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        height: 40%;
        width: 100%;
    }

    .content {
        color: var(--foreground1);
        border-top: solid 3px var(--background2);
        padding: 0.5rem;
        margin-top: -5px;
        margin-bottom: 5px;
    }
}

section.project-slide {
    color: var(--foreground2);
    margin: 1rem auto;
    font-size: 1.5rem;

    .record {
        height: inherit;
        background-color: rgba(0, 0, 0, 0.8);
        border-radius: 10px;
        border-color: var(--highlight);
        border-width: 20px;
        padding: 3rem;

        h2 {
            margin: 0.5rem 0;
            color: var(--foreground2);
        }

        .pill {
            border-radius: 500px;
            border-style: solid;
            border-width: 1px;
            border-color: var(--foreground2);
            background-color: var(--highlight);
            color: var(--foreground2);
            padding: 3px 12px;
            margin: 3px;
            font-size: 0.8rem;
            font-weight: 500;
            width: fit-content;
            align-content: center;
            text-align: center;
        }
    }
}